<template>
	<view class="" style="display: flex;flex-flow: column nowrap;align-items: center;width: 750upx; ">
		<navigationview :current="current" :values="items" v-on:clickItem="onClickItem" :list_id="list_id"></navigationview>


		<view class="" v-show="current === 0" style=" width: 650upx; height: 50upx; flex-flow: row nowrap; background-color:white;justify-content:space-between;padding:0upx 50upx;align-items: center;z-index: 31; border-bottom: 1upx solid #EFEFEF ;">

			<picker class="icontext" style="width: 80upx;font-size: 23upx;" :range="filter_fanwei" @change="pickerChange">
				{{filter_picker_name}}&nbsp;&nbsp;&#xe602;
			</picker>
		</view>




		<scroll-view scroll-y class="jichukuangjia" :style="'height:'+height+'px'  " v-show="current === 0">
			
			<view class="" style="width: 100%;height: 100upx;background-color: white;flex-flow: row; padding:0upx 15upx;justify-content:flex-start;align-items: center;">
				<image class="leixing_view" src="../../../static/leixing/line.png"></image>
				<image class="leixing_view" src="../../../static/leixing/pie.png"></image>
			</view>
			
			
			<view class="timeline " style="background-color: #fff;">
				<view class="timeline-item timeline-first-item ">
					<view class="timeline-item-keynode ">{{item_card_event_list[0].event_time}}</view>
					<view class="timeline-item-divider "></view>
					<view class="timeline-item-content " @click="tap_item(item_card_event_list[0])">{{item_card_event_list[0].event_name}}</view>
					<view class=" " style="width: 50upx; position: absolute; right: 30upx; font-size: 20upx; justify-content: space-between;">
						<view class="icontext" @click="click_dianzhan(0)">{{item_card_event_list[0].can_dianzhan!='false' ? '&#xe610;':'&#xe60c;'}}{{item_card_event_list[0].dianzhan}}</view>
					</view>
				</view>

				<view class="timeline-item" v-for="(item,index) in item_card_event_list" :key="index" v-if="index>0&&index<(item_card_event_list.length-1)">
					<view class="timeline-item-keynode">{{item_card_event_list[index].event_time}}</view>
					<view class="timeline-item-divider"></view>
					<view class="timeline-item-content" @click="tap_item(item)">{{item_card_event_list[index].event_name}}</view>
					<view class=" " style="width: 50upx; position: absolute; right: 30upx; font-size: 20upx; justify-content: space-between;">
						<view class="icontext" @click="click_dianzhan(index)">{{item_card_event_list[index].can_dianzhan!='false' ? '&#xe610;':'&#xe60c;'}}{{item_card_event_list[index].dianzhan}}</view>
					</view>
				</view>

				<view class="timeline-item timeline-last-item">
					<view class="timeline-item-keynode">{{item_card_event_list[item_card_event_list.length-1].event_time}}</view>
					<view class="timeline-item-divider"></view>
					<view class="timeline-item-content" @click="tap_item(item_card_event_list[item_card_event_list.length-1])">{{item_card_event_list[item_card_event_list.length-1].event_name}}</view>
					<view class=" " style="width: 50upx; position: absolute; right: 30upx; font-size: 20upx; justify-content: space-between;">
						<view class="icontext" @click="click_dianzhan(item_card_event_list.length-1)">{{item_card_event_list[item_card_event_list.length-1].can_dianzhan!='false' ? '&#xe610;':'&#xe60c;'}}{{item_card_event_list[item_card_event_list.length-1].dianzhan}}</view>
					</view>
				</view>
			</view>
			<view class="" style="flex-flow: column;justify-content: center;align-content: center;align-items: center;">
				<view @click="gotonewitem" style="font-size: 24upx;">
					<view class="icontext" style="font-size: 74upx;color: #007AFF;">
						&#xe641;
					</view>
					<view class="icontext" style="font-size: 34upx;">
						创建
					</view>
				</view>
			</view>
		</scroll-view>


		<scroll-view scroll-y class="jichukuangjia" :style="'height:'+height+'px' " v-if="current === 1">
			<handleview :card_id="luyouData[list_id].card_id"></handleview>
		</scroll-view>


	</view>
</template>

<script>
	import {
		mapState,
		mapMutations
	} from 'vuex'
	import navigationview from '../../../components/navigation.vue';
	import handleview from '../../../components/handle-view.vue';
	export default {
		data() {
			return {
				luyouData: [{}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {},
					{}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {},
				],
				list_id: null,
				x: -200,
				y: -200,
				items: [
					'时间线',
					'改进',
				],
				filter_picker_name: "筛选",
				mulu_flag: 'null',
				height: 0,
				current: 0,
				filter_fanwei: null,
				item_card_event_list: [{
					"event_time": "",
					"event_name": ""
				}, ],


			}
		},
		computed: {
			...mapState(['hasLogin', 'userInfo', 'phonenum', 'openid'])
		},
		components: {

			handleview,
			navigationview,
		},
		onLoad: async function(option) {
			console.log("list_id:" + option.list_id)
			this.list_id = option.list_id;
			let aa = await this.get_hot_card_list();
			this.height = uni.getSystemInfoSync().windowHeight - 120 * (uni.getSystemInfoSync().windowWidth / 750);
			setTimeout(() => this.getData_list(), 500);
		},

		methods: {
			onClickItem(index) {
				if (this.current !== index) {
					this.current = index;
				}
			},

			pickerChange(e) {
				var that = this;
				// console.log(e.detail.value)
// 				if (that.filter_fanwei != null) {
// 					console.log(that.filter_fanwei[e.detail.value])
// 					uni.showLoading({
// 						title: '数据整理'
// 					})
// 					that.data_list = [];
// 					for (let i = 0; i < that.data_list_xiazai.length; i++) {
// 						if (that.data_list_xiazai[i][that.filter_name] == that.filter_fanwei[e.detail.value]) {
// 							that.data_list.push(that.data_list_xiazai[i])
// 						}
// 					}
// 					setTimeout(function() {
// 						uni.hideLoading();
// 						that.$forceUpdate();
// 						that.filter_picker_name = that.filter_fanwei[e.detail.value];
// 					}, 200);
// 			
// 				}
			
			
			
			},
			
			
			
			click_dianzhan(e) {
				if (this.item_card_event_list[JSON.stringify(e)].can_dianzhan != "false") {
					console.log(JSON.stringify(e));
					this.item_card_event_list[JSON.stringify(e)].dianzhan = Number(this.item_card_event_list[JSON.stringify(e)].dianzhan) +
						1;
					this.item_card_event_list[JSON.stringify(e)].can_dianzhan = "false"
					this.pushData_dianzhan(this.item_card_event_list[JSON.stringify(e)])
				} else {
					uni.showToast({
						title: "谢谢!已经投票过啦",
						icon: "none"
					})
				}
			},

			pushData_dianzhan: function(comment_info) {
				uni.request({
					url: "https://kaikaiomg.applinzi.com/uni_app_upload.php",
					method: "POST",
					data: {
						'card_id': this.luyouData[this.list_id].card_id,
						'form': this.luyouData[this.list_id].updata_data_title,
						'dianzhan_num': comment_info.dianzhan,
						'item_id': Number(comment_info.item_id),
						'fandui': comment_info.fandui,
						'nickName': this.userInfo.nickName,
						'phonenum': this.phonenum,
						'openid': this.openid,
						'avatarUrl': this.userInfo.avatarUrl,
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: (res) => {
						console.log('success_update:', this.luyouData[this.list_id].updata_data_title)
					},
					fail: () => {
						uni.showModal({
							content: "请求失败，请重试!",
							showCancel: false
						})
					}
				})
			},








			gotonewitem: function() {
				uni.navigateTo({
					url: "../item_detail/item_detail?data={}&card_id=" + this.luyouData[this.list_id].card_id +
						"&new_item_flag=true",
				})
			},

			tap_item: function(item) {
				uni.navigateTo({
					url: "../item_detail/item_detail?data=" + JSON.stringify(item) + "&card_id=" + this.luyouData[this.list_id].card_id,
				})
			},
			getData_list() {
				uni.request({
					url: 'https://kaikaiomg.applinzi.com/uni_app_download.php',
					method: "POST",
					data: {
						'form': this.luyouData[this.list_id].getdata_title,
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: (ret) => {
						if (ret.statusCode !== 200) {
							console.log("请求失败:", ret)
						} else {
							console.log("下载数据:", ret.data)
							this.item_card_event_list = ret.data.data;
						}
					}
				});
			},

		}
	}
</script>

<style>
	.page {
		background: #efeff4;
	}

	.title {
		padding: 20upx;
	}

	.timeline {
		margin: 35upx 0;
		display: flex;
		flex-direction: column;
		position: relative;
	}
	.leixing_view {
		flex-flow: column;
		width: 60upx;
		height: 60upx;
        /* border: 3px solid black ; */
        /* border-radius: 60upx; */
		margin: 0upx 12upx;
		background-color: ;
	}

    .rank_title_bac {
		padding: 15upx;
		/* border: 1upx solid blue; */
		border-radius: 7upx;
		background-color: #DCDCDC;
		font-size: 25upx;
		margin: 10upx;
		color: black;
	}
	.timeline-item {
		display: flex;
		flex-direction: row;
		position: relative;
		padding-bottom: 20upx;
		box-sizing: border-box;
		overflow: hidden;

	}

	.timeline-item .timeline-item-keynode {
		width: 100upx;
		flex-shrink: 0;
		box-sizing: border-box;
		padding-right: 20upx;
		text-align: right;
		font-size: 20upx;
	}

	.timeline-item .timeline-item-divider {
		flex-shrink: 0;
		position: relative;
		width: 20upx;
		height: 30upx;
		top: 5upx;
		border-radius: 50%;
		background-color: #bbb;
	}



	.timeline-item-divider::before,
	.timeline-item-divider::after {
		position: absolute;
		left: 10upx;
		width: 1upx;
		height: 100vh;
		content: '';
		background: inherit;
	}

	.timeline-item-divider::before {
		bottom: 100%;
	}

	.timeline-item-divider::after {
		top: 100%;
	}


	.timeline-last-item .timeline-item-divider:after {
		display: none;
	}

	.timeline-first-item .timeline-item-divider:before {
		display: none;
	}

	.timeline-item .timeline-item-content {
		padding-left: 20upx;
		font-size: 24upx;
		width: 400upx;
	}




	/* 自定义节点颜色 */
	.timeline-last-item .timeline-item-divider {
		background-color: #1AAD19;
	}



	.jichukuangjia {
		display: flex;
		/*   position: fixed;
				bottom: 0upx; */
		/* 		background-image: url('http://kaikaiomg-kaikaiomg.stor.sinaapp.com/uni_app_image/bc.jpg'); */
		background-color: white;
		width: 750upx;
		overflow: hidden;
		flex-flow: column nowrap;
		position: fixed;
		bottom: 0upx;
		/* justify-content: center; */
		/* align-items: center; */
	}

	.handle_view {
		position: fixed;
		width: 100%;
		height: 90upx;
		min-height: 90upx;
		left: 0upx;
		bottom: 0upx;
		overflow: hidden;
	}
</style>



